import App from './App'

Vue.use(VueRouter)
Vue.use(VueResource)

Vue.http.interceptors.push({

  request: function (request) {
    request.headers['x-access-token'] = 'eyJ0eXAiOiJKV1QiLCJhbGciOiJIUzI1NiJ9.eyJpZCI6IjU2OWNkNDQ1MDgyZGRkZmUxMDg1OWU4MyIsIm5hbWUiOiJZYW5nIE1UIiwiZW1haWwiOiJ0aW1lc3dpbmRAMTYzLmNvbSIsInRlYWNoZXIiOiI1NmE3NGJiZjk4YmY0YzY4MDg0MTc3NjgiLCJyb2xlIjoidGVhY2hlciIsImlhdCI6MTQ1ODIyNDAyMCwiZXhwIjoxNDU4MzEwNDIwfQ.vtCvHRtDndBEW-AXv2_7Z9nmx3SN3wibQ8P37nTpn1k'
    return request
  },
  response: function (response) {
    return response
  }

})

var router = new VueRouter()

router.map({
  '/': {
    name: 'home',
    component: function (resolve) {
      require(['./components/Home'], resolve)
    }
  }
})

router.start(App, '#app')



